    h3 tica en la investigacin de mercados
    p.mb-5 Los dos factores ticos ms importantes para la investigacin son la confidencialidad y honestidad del cliente. Cada uno de ellos es cuestin de tica. La tica se define como los principios o valores morales que por lo general gobiernan la conducta de un individuo o grupo. La conducta tica no es, sin embargo, una relacin unidireccional. Clientes y proveedores, as como trabajo de campo, tambin deben actuar en forma tica. Las cuestiones ticas van desde consideraciones prcticas estrechamente definidas, como la obligacin de un investigador de ser honesto con sus clientes, a cuestiones sociales y filosficas ms amplias, como la responsabilidad de una compaa de preservar el medio ambiente y proteger los derechos de los empleados. Muchos problemas ticos se desarrollan de conflictos entre los diferentes intereses de los dueos de la compaa y sus trabajadores, clientes y comunidad circundante. Los gerentes deben equilibrar lo ideal con lo prctico: la necesidad de producir una ganancia razonable para los accionistas de la compaa con honestidad en las prcticas de negocios, y con consideraciones ambientales y sociales ms amplias. (McDaniel,2016)
