    #t_1_1_1.titulo-segundo
      h2 1.1 Investigacin cualitativa
    .row.mb-5.justify-content-center
      .col-6
        p Toda investigacin inicia con una idea que se desarrolla paulatinamente. Para comenzar una investigacin se necesita conocer las rutas que han sido construidas por las comunidades cientficas para estudiar cualquier tema, fenmeno o planteamiento. Segn (Sampieri, 2018) hay tres rutas fundamentales: la cuantitativa, la cualitativa y la mixta. 

          br
          br
          |La investigacin cualitativa se enfoca en comprender los fenmenos, explorndolos desde la perspectiva de los participantes en su ambiente natural y en relacin con el contexto. El enfoque cualitativo se selecciona cuando el propsito es examinar la forma en que ciertos individuos perciben y experimentan fenmenos que los rodean, profundizando en sus puntos de vista, interpretaciones y significados (Lindlof y Taylor, 2018; Punch, 2014, Lichtman, 2013, Morse, 2012, Encyclopedia of Educational Psychology, 2008).
          br
          br
          |Una investigacin cualitativa se requiere cuando las necesidades de informacin demandan argumentos especficos, evaluacin de reacciones, profundizacin en los conceptos y valores, deteccin de motivadores y valoracin de las emociones generadas con un planteamiento. este enfoque precisa el soporte y ejecucin de un profesional en el rea de psicologa, idealmente con conocimientos de mercado, conducta de consumidor, entre otro (Martinez,2011)
      .col-3
        figure.mb-5
          img.mb-3(src='@/assets/curso/img17.jpg', alt='Texto que describa la imagen')
    p.mb-5 Las limitaciones de la investigacin cualitativa segn McDaniel (2016) son:
