    p.mb-5 En la investigacin cualitativa se emplean mtodos de recoleccin de datos que son no cuantitativos, con el propsito de explorar las relaciones sociales y describir la realidad tal como se experimenta. Requiere un profundo entendimiento del comportamiento humano y las razones que lo gobiernan, podra usarse para examinar las actitudes, sentimientos y motivaciones del usuario.
    #t_1_2.titulo-segundo
      h2 1.2 Investigacin cuantitativa
    .row.mb-5.justify-content-center
      .col-6
        figure.mb-5
          img.mb-3(src='@/assets/curso/img19.svg', alt='Texto que describa la imagen')

    p.mb-5 La investigacin cuantitativa es diseada para recopilar informacin de muestras estadsticamente representativas de una poblacin objetivo y es usualmente llevada a cabo entre varios cientos, a veces miles de encuestados, por esta razn la informacin es generalmente obtenida usando cuestionarios de estructura estandarizada. Este tipo de informacin es analizada con procesos estadsticos (Palmer, 2012)
      br
      br
      |Una investigacin cuantitativa se hace necesaria cuando los objetivos planteados estn orientados a encontrar cifras, cuantificar intenciones y formas de proceder, establecer proporciones, diferencias e importancia relativa de los factores evaluados. esta investigacin requiere el proceso de muestreo y el soporte de la estadstica para buscar informacin representativa y suficiente del mercado en estudio. (Martinez, 2011)
      br
      br
      |Permite examinar los datos de manera numrica, especialmente en el campo de la estadstica. Se requiere que entre los elementos del problema de investigacin exista una relacin cuya naturaleza sea lineal, que sea posible definirlo, limitarlo y saber hacia dnde dirige.
