    p.mb-5 En algunos casos se realiza la investigacin mixta que entrelaza a las dos anteriores (cuantitativa y cualitativa) y las mezcla, pero es ms que la suma de las dos anteriores e implica su interaccin y potenciacin. Los mtodos mixtos representan un conjunto de procesos sistemticos, empricos y crticos de investigacin e implican la recoleccin y el anlisis de datos tanto cuantitativos como cualitativos, as como su integracin y discusin conjunta, para realizar inferencias producto de toda la informacin recabada y lograr un mayor entendimiento del fenmeno bajo estudio. (Hernndez-Sampieri y Mendoza, 2008). 
    #t_1_3.titulo-segundo
      h2 1.3 Proceso de investigacin de mercados  
    .row.mb-5.justify-content-center
      .col-3.d-block.my-auto
        p El proceso de investigacin puede entenderse como una secuencia general de seguir para as poder disear y llevar a cabo una investigacin (Brown y Suter, 2012).       
      .col-7     
        figure.mb-5
          img.mb-3(src='@/assets/curso/img21.svg', alt='Texto que describa la imagen')
    .row.mb-5.justify-content-center
