          img.mb-3(src='@/assets/curso/img22.svg', alt='Texto que describa la imagen')
    .cuadros--azul.p-4.mb-3
      h3.text-white.text-center.mb-0 PASOS
    .cuadros--gris.p-5.mb-5 
      .d-flex.mb-3
        .lista-ol--cuadro__vineta
          span 1
        p
          b Definir el problema a investigar. 
          | El primer paso en cualquier proyecto de investigacin de mercados es definir el problema. Al hacerlo, el investigador debe considerar el propsito del estudio, la informacin antecedente pertinente, la informacin que se necesita y la forma en que se utilizar para la toma de decisiones. Supone hablar con quienes toman las decisiones, entrevistas con los expertos del sector, anlisis de los datos secundarios y, quizs, alguna investigacin exploratoria. Incluye la formulacin de un marco de referencia, objetivos, alcance y preguntas de investigacin e hiptesis. (Malhotra, 2008).
      .d-flex
        .lista-ol--cuadro__vineta
          span 2
        p
          b Seleccionar y establecer el diseo de la investigacin. 
          | Incluye los siguientes pasos:
          br
          br
          ul.lista-ul--color.mb-5.ms-5
              li 
                i.fas.fa-angle-right
                | Definicin del diseo de investigacin, exploratoria, descriptiva o causal.
              li 
                i.fas.fa-angle-right
                | Anlisis de datos secundarios
              li 
                i.fas.fa-angle-right
                | Tcnicas para la obtencin de datos cualitativos y cuantitativos. 
              li 
                i.fas.fa-angle-right
                | Diseo de cuestionarios. 
              li 
                i.fas.fa-angle-right
                | Proceso de muestreo y tamao de la muestra.
              li 
                i.fas.fa-angle-right
                | Plan para el anlisis de datos. 
      .d-flex.mb-3
        .lista-ol--cuadro__vineta
          span 3
        p
          b Recoleccin de datos y anlisis. 
          | La recopilacin de datos implica contar con personal o un equipo que opere ya sea en el campo o electrnicamente el desarrollo de la investigacin. La seleccin, capacitacin, supervisin y evaluacin adecuadas del equipo de campo ayuda a minimizar los errores en la recopilacin de datos. La preparacin de los datos incluye su revisin, codificacin, transcripcin y verificacin. Cada cuestionario o forma de observacin se revisa y se corrige. Se asignan cdigos numricos o letras para representar cada respuesta a cada pregunta del cuestionario. Los datos se procesan y se analizan para la toma de decisiones administrativas. (Malhotra, 2008). 
      .d-flex.mb-3
        .lista-ol--cuadro__vineta
          span 4
        p
          b Formular hallazgos. 
          | Todo el proyecto debe documentarse en un informe escrito donde se presenten las preguntas de investigacin, el enfoque, el diseo y los procedimientos utilizados para la recopilacin y anlisis de datos; y donde se incluyan los resultados y los principales resultados. Los hallazgos deben presentarse en un formato comprensible para la toma de decisiones. (Malhotra, 2008). 
      .d-flex.mb-3
        .lista-ol--cuadro__vineta
          span 5
        p
          b Seguimiento y control de <i>marketing</i>. 
          |  Cuando ya est lista la Investigacin de mercados es necesario realizar seguimiento continuo a la informacin adquirida para realizar ajustes y modificaciones en los posibles problemas que se presenten.
    h5 Definir el problema a investigar
