    p.mb-5 Al definir el problema a investigar es necesario recopilar la informacin realizando una investigacin exploratoria a continuacin McDaniel y Gates (2016) explican el paso a paso:
    TabsB.color-acento-contenido.mb-5
      .py-4.py-md-5(titulo="Conocer el entorno en la toma de decisiones" :icono="require('@/assets/curso/img84.svg')")
        .row.justify-content-center
          .col-md-8.mb-4.mb-md-0
            h4 Conocer el entorno en la toma de decisiones
            p Se necesita informacin contextual adicional para entender plenamente el problema. Esto podra significar hablar simplemente con los gerentes de marca o los gerentes de nuevos productos, leer informes de la compaa, visitar centros de produccin y tiendas minoristas y quiz hablar con proveedores. Cuanto mejor conozca el investigador de mercados el entorno de la toma de decisiones, lo que incluye la industria, la empresa, sus productos o servicios y el mercado objetivo, ser ms probable que el problema se defina correctamente. Este paso puede llamarse realizar un anlisis de la situacin. La investigacin preliminar o exploratoria puede adoptar varias formas: estudios piloto, encuestas de experiencia, anlisis de datos secundarios, anlisis de casos de estudios piloto y grupos foco. 
          .col-md-2
            figure
              img(src='@/assets/curso/img90.svg', alt='Texto que describa la imagen')
      .py-4.py-md-5(titulo="Anlisis de encuestas de experiencia" :icono="require('@/assets/curso/img85.svg')")
        .row.justify-content-center
          .col-md-8.mb-4.mb-md-0
            h4 Anlisis de encuestas de experiencia
            p Implican hablar con individuos conocedores, tanto dentro como fuera de la organizacin, que pueden ofrecer discernimientos sobre el problema. El investigador puede tener simplemente una lista de temas por abordar como una conversacin informal.
          .col-md-2
            figure
              img(src='@/assets/curso/img91.svg', alt='Texto que describa la imagen')
      .py-4.py-md-5(titulo="Anlisis de datos secundarios" :icono="require('@/assets/curso/img86.svg')")
        .row.justify-content-center
          .col-md-8.mb-4.mb-md-0
            h4 Anlisis de datos secundarios
            p El anlisis de datos secundarios es otra forma de investigacin exploratoria, los datos secundarios se han recopilado con algn propsito distinto al inmediato. Se puede usar internet para acceder a incontables fuentes de datos secundarios de manera rpida y a un costo mnimo. 
          .col-md-2
            figure
              img(src='@/assets/curso/img92.svg', alt='Texto que describa la imagen')
      .py-4.py-md-5(titulo="Anlisis de casos" :icono="require('@/assets/curso/img87.svg')")
        .row.justify-content-center
          .col-md-8.mb-4.mb-md-0
            h4 Anlisis de casos
            p El anlisis de datos secundarios es otra forma de investigacin exploratoria, los datos secundarios se han recopilado con algn propsito distinto al inmediato. Se puede usar internet para acceder a incontables fuentes de datos secundarios de manera rpida y a un costo mnimo. 
          .col-md-2
            figure
              img(src='@/assets/curso/img93.svg', alt='Texto que describa la imagen')
      .py-4.py-md-5(titulo="Grupos de enfoque" :icono="require('@/assets/curso/img88.svg')")
        .row.justify-content-center
          .col-md-8.mb-4.mb-md-0
            h4 Grupos de enfoque
            p Los grupos de enfoque son conversaciones en profundidad, usualmente compuestas de 8 a 12 participantes, dirigidas por un moderador y generalmente limitadas a un concepto, idea o tema particular. La idea general es hacer que lo que una persona dice genere pensamientos y comentarios de otras, creando de este modo una dinmica de grupos. Es decir, la interaccin de las respuestas rendir ms informacin que si el mismo nmero de personas hubiera contribuido en entrevistas individuales. 
          .col-md-2
            figure
              img(src='@/assets/curso/img94.svg', alt='Texto que describa la imagen')
      .py-4.py-md-5(titulo="Uso de intranets" :icono="require('@/assets/curso/img89.svg')")
        .row.justify-content-center
          .col-md-8.mb-4.mb-md-0
            h4 Uso de intranets
            p El computador puede ser una herramienta muy poderosa en organizaciones muy grandes con intranets, el investigador tiene la capacidad de determinar si informacin necesaria o relevante.
          .col-md-2
            figure
              img(src='@/assets/curso/img95.svg', alt='Texto que describa la imagen')















































    p.mb-5 Uno de los mtodos utilizados para definir y formular el problema a investigar que indican las razones que originan la necesidad de investigar, plantean las preguntas destacadas y justifican la necesidad de hacer la investigacin es:
    h5 Mtodo del rbol de problemas
    p.mb-5 El rbol de problemas es una tcnica que se emplea para identificar una situacin negativa (problema central), la cual se intenta solucionar analizando relaciones de tipo causa-efecto. Para ello, se debe formular el problema central de modo tal que permita diferentes alternativas de solucin, en lugar de una solucin nica.
    .row.mb-5.justify-content-center
      .col-lg-10
        figure.mb-5
          img.mb-3(src='@/assets/curso/img27.svg', alt='Texto que describa la imagen')
    p.mb-5 Luego de haber sido definido el problema central, se exponen tanto las causas que lo generan como los efectos negativos producidos, y se interrelacionan los tres componentes de una manera grfica.
      br
      br
      | La tcnica adecuada para relacionar las causas y los efectos, una vez definido el problema central, es la lluvia de ideas. Esta tcnica consiste en hacer un listado de todas las posibles causas y efectos del problema que surjan, luego de haber realizado un diagnstico sobre la situacin que se quiere resolver.   
    .row.mb-5.justify-content-center
      .col-lg-10
        figure.mb-5
          img.mb-3(src='@/assets/curso/img28.svg', alt='Texto que describa la imagen')
          figcaption Fuente: UNESCO, Organizacin de las naciones unidas para la educacin, la ciencia y la cultura.
    h5 Objetivos
    p.mb-5 Los objetivos de la investigacin deben basarse en la definicin del problema de investigacin. Unos objetivos de investigacin enunciados formalmente representan una gua para decidir qu pasos hay que dar. La premisa es que si se alcanzan los objetivos, el que decide tiene la informacin necesaria para resolver el problema (Hair, 2010).
      br
      br
      |Un objetivo claro significa que sea de fcil comprensin, es decir, que se le pueda encontrar rpidamente el sentido; y concreto, que su redaccin sea corta, precisa, a fin de evitar largos contenidos que causan confusin. Con la definicin del objetivo, el investigador identifica adems el segmento meta al cual va a dirigir la investigacin (Prettel, 2016).
    .row.mb-5.justify-content-center
      .col-lg-10
        figure.mb-5
          img.mb-3(src='@/assets/curso/img29.jpg', alt='Texto que describa la imagen')
    p.mb-5 Los objetivos de la investigacin deben basarse en la definicin del problema de investigacin. Unos objetivos de investigacin enunciados formalmente representan una gua para decidir qu pasos hay que dar. La premisa es que si se alcanzan los objetivos, el que decide tiene la informacin necesaria para resolver el problema (Hair, 2010).
      br
      br
      |Un objetivo claro significa que sea de fcil comprensin, es decir, que se le pueda encontrar rpidamente el sentido; y concreto, que su redaccin sea corta, precisa, a fin de evitar largos contenidos que causan confusin. Con la definicin del objetivo, el investigador identifica adems el segmento meta al cual va a dirigir la investigacin (Prettel, 2016).
    .row.mb-5.justify-content-center
      .col-lg-5
        .cuadros--salmon.p-4
          h5.mb-0.text-white.text-center Objetivo general
        .d-flex
          .cuadros--gris-fit.d-block.p-5
            p.my-auto.d-block Se refiere a la informacin que a nivel macro se pretende conseguir. es un planteamiento global, derivado de la inquietud original, que abarca y envuelve de manera concreta toda la necesidad de la empresa que requiere la investigacin. (Martinez,2011)
      .col-lg-5
        .cuadros--rojo.p-4
          h5.mb-0.text-white.text-center Objetivos especficos
        .d-flex
          .cuadros--gris-fit.d-block.p-5
            p.my-auto Teniendo claridad sobre el objetivo general, este debe ser desplegado en las inquietudes particulares que garanticen la respuesta a todas las cuestiones planteadas. Hay que estructurarlos pensando de lo general a lo particular, empezando con las necesidades de informacin ms globales, hasta terminar en el detalle, que normalmente se refiere a aspectos ms micro sobre la informacin. (Martinez,2011)
    p.mb-5 Teniendo claridad sobre el objetivo general, este debe ser desplegado en las inquietudes particulares que garanticen la respuesta a todas las cuestiones planteadas. Hay que estructurarlos pensando de lo general a lo particular, empezando con las necesidades de informacin ms globales, hasta terminar en el detalle, que normalmente se refiere a aspectos ms micro sobre la informacin. (Martinez,2011)
    .row.mb-5.justify-content-center
      .col-lg-4
        .cuadros--rojo.p-4
          h5.mb-0.text-white.text-center Objetivo Social
        .d-flex
          .cuadros--gris-fit.d-block.p-5.text-center

            p.my-auto.d-block Tiene en cuenta consumidores, productores, productos o servicios en relacin a expectativas y necesidades de los clientes.
      .col-lg-4
        .cuadros--coral.p-4
          h5.mb-0.text-white.text-center Objetivo Econmico
        .d-flex
          .cuadros--gris-fit.d-block.p-5.text-center

            p.my-auto Aclara alternativas de beneficio, utilidad o rentabilidad econmica.
      .col-lg-4
        .cuadros--naranja.p-4
          h5.mb-0.text-white.text-center Objetivo Administrativo
        .d-flex
          .cuadros--gris-fit.d-block.p-5.text-center

            p.my-auto Facilita la toma de decisiones gerenciales con base en las necesidades de los clientes.
    h5 Alcance
    p.mb-5 El alcance de la investigacin de mercados es bastante amplio, puede darse desde observaciones, entrevistas personales, experimentos, estudios de caso, hasta enfoques etnogrficos, tratando de encontrar la forma como se comporta el consumidor o la persona objeto de anlisis segn la meta por alcanzar. (Prieto,2013)
    .row.mb-5.justify-content-center
      .col-lg-8
        figure.mb-5
          img.mb-3(src='@/assets/curso/img30.svg', alt='Texto que describa la imagen') 
    p.mb-5 Para Santiago Martnez Vela (2011), el pblico objetivo hace parte del alcance de la investigacin, ya que genera los parmetros que harn que una u otra persona sea elegible para participar de la muestra diseada y por ello est clasificado en dos tipos:
    .row.mb-5.justify-content-center
      .col-lg-5
        .cuadros--salmon.p-4
          h5.mb-0.text-white.text-center Del producto/servicio
        .d-flex
          .cuadros--gris-fit.d-block.p-5.text-center
            p.my-auto.d-block define quin es el consumidor, cliente o influenciador actual del producto o servicio.
      .col-lg-5
        .cuadros--rojo.p-4
          h5.mb-0.text-white.text-center De la investigacin
        .d-flex
          .cuadros--gris-fit.d-block.p-5.text-center
            p.my-auto estima quin deber ser parte de la muestra para lograr los objetivos. 
    p.mb-5 Algunos ejemplos son: deseos, gustos, preferencias, actitudes, opiniones, percepciones, recordacin y creatividad en distintos temas relacionados a la vida de los seres humanos y su desarrollo en el mundo.
    h5 Necesidades de informacin
    .row.mb-5.justify-content-center
      .col-lg-4
        figure.mb-5
          img.mb-3(src='@/assets/curso/img31.svg', alt='Texto que describa la imagen') 
    p.mb-5 Uno de los pasos ms importantes del proyecto es desglosar el problema en preguntas de investigacin, porque el cmo se defina el problema de la investigacin influye en el resto de los pasos. La tarea del investigador es volver a enunciar las variables originales del problema en la forma de preguntas bsicas: cmo, qu, dnde, cundo y por qu. (Hair,2010)
      br
      br
      |A continuacin, se mencionan algunos ejemplos de preguntas claves para el desarrollo de la investigacin:
    .row.mb-1.justify-content-center
      .col-lg-5
        .tarjeta-avatar-b.mb-2
