    h5 Establecer el diseo investigacin
    p.mb-5 Para establecer el diseo de la investigacin es necesario tener la siguiente secuencia:
    .d-flex.mb-3.p-5
        .lista-ol--cuadro__vineta
          span 1
        p
          b Investigacin exploratoria: 
          | busca indicios para obtener informacin, se realiza una Investigacin preliminar en la cual se recolecta informacin primaria y secundaria mediante mecanismos informales y no estructurados.
          br
          br
          |El principal objetivo de la investigacin exploratoria consiste en proporcionar una idea preliminar del problema o situacin que confronta el investigador. Este tipo de investigacin se utiliza normalmente para identificar cursos de accin relevantes, obtener conocimientos adicionales antes de desarrollar un enfoque o definir el problema de forma ms precisa. (Rosendo, 2018).
    .row.mb-5.justify-content-center
      .col-lg-8
        figure.mb-5
          img.mb-3(src='@/assets/curso/img32.svg', alt='Texto que describa la imagen') 
          figcaption Fuente: Estructurada con base en lo planteado por: Naresh k. Malhotra, (2008)
    .row.mb-5.justify-content-center
      .col-lg-8
        .tarjeta--gris.p-5 
          p.mb-5 El objetivo de la investigacin exploratoria es explorar o examinar una situacin para brindar conocimiento y comprensin, y puede utilizarse para cualquiera de los siguientes propsitos:
          ul.lista-ul--color.mb-5
            li 
              i.fas.fa-angle-right
              | Formular un problema o definirlo con mayor precisin.
            li 
              i.fas.fa-angle-right
              | Identificar cursos alternativos de accin.
            li 
              i.fas.fa-angle-right
              | Desarrollar hiptesis.
            li 
              i.fas.fa-angle-right
              | Obtener ideas para desarrollar un enfoque del problema
            li 
              i.fas.fa-angle-right
              | Establecer prioridades para la investigacin posterior
    p.mb-5| Jorge Eliecer Prieto (2013) define cuatro mtodos para realizar investigacin exploratoria:
    .row.mb-1.justify-content-center
      .col-lg-5
        .tarjeta-avatar-b.mb-2
          img(src='@/assets/curso/img33.svg' alt='AvatarTop')
          .tarjeta.tarjeta--gris
            .p-4.py-auto.d-block
              p Encuestas de experiencia o entrevista en profundidad (preguntas a personas expertas en un tema especfico).      
      .col-lg-5
        .tarjeta-avatar-b.mb-2
          img(src='@/assets/curso/img34.svg' alt='AvatarTop')
          .tarjeta.tarjeta--gris
            .p-4.py-auto.d-block
              p Anlisis de datos secundarios (revisin de investigaciones similares).
    .row.mb-5.justify-content-center
      .col-lg-5
        .tarjeta-avatar-b.mb-2
          img(src='@/assets/curso/img35.svg' alt='AvatarTop')
          .tarjeta.tarjeta--gris
            .p-4.py-auto.d-block
              p Estudios de caso (averiguacin intensiva sobre situaciones afines).
      .col-lg-5
        .tarjeta-avatar-b.mb-2
          img(src='@/assets/curso/img36.svg' alt='AvatarTop')
          .tarjeta.tarjeta--gris
            .p-4.py-auto.d-block
              p Estudios piloto o grupos foco (investigacin a pequea escala con muestreo, poco rigurosa).
    .d-flex.mb-3.p-5
        .lista-ol--cuadro__vineta
          span 2
        p
          b Investigacin Concluyente: 
          | El objetivo de la investigacin concluyente es probar hiptesis especficas y examinar relaciones de causa-efecto. Esto requiere que la informacin necesaria est claramente especificada. Se basa en grandes muestras representativas, y los datos se analizan normalmente mediante anlisis cuantitativos. Los hallazgos se consideran concluyentes por naturaleza y pueden utilizarse para la toma de decisiones gerenciales. (Rosendo, 2018). 
    .row.mb-1.justify-content-center
      .col-lg-10
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/img33.svg' alt='AvatarTop').img-top
          .tarjeta.tarjeta--gris
            .p-4.py-auto.d-block
              h6 Descriptiva 
              p Etapa que busca describir los problemas o indicios con mayor detalle, cuantificando el conjunto de mtodos y procedimientos que describen a las variables de <i>marketing</i>.
                br
                br
                |Es aquella que busca una representacin grfica de caractersticas y fenmenos del mercado y la determinacin de la frecuencia con que se usan, tratando de hacer predicciones y determinar el grado de asociacin entre sus variables, tal como ocurre en los estudios de perfil de consumidores, potencial del mercado y utilizacin de un producto seccionado. Las fuentes de datos ms usadas en este tipo de investigacin son el cuestionario a personas seleccionadas, los datos secundarios y la simulacin de escenarios. (Prieto, 2013).
                br
                br
                |Consiste en recopilar datos numricos para responder preguntas de investigacin; da respuestas a las preguntas sobre quin, qu, cundo, dnde y cmo. En <i>marketing</i>, los ejemplos de la informacin descriptiva incluyen actitudes, intenciones, preferencias, hbitos de compra, evaluacin de las estrategias actuales de mezcla de <i>marketing</i> y datos demogrficos de los consumidores. (Hair, 2010).
      .col-lg-10
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/img37.svg' alt='AvatarTop').img-top
          .tarjeta.tarjeta--gris
            .p-4.py-auto.d-block
              h6 Causal
              p Penetra en la realidad del estudio para determinar las causas que han originado el problema, se controlar varios factores para determinar cul de ellos es el causante del problema y tomar decisiones.
                br
                br
                |Es aquella que permite obtener informacin relevante sobre la relacin casusa-efecto en un sistema de mercadeo, como las que se hacen para mirar la incidencia en los volmenes de venta de los cambios en los niveles de precio. Las fuentes de datos ms usadas en este tipo de investigacin son el cuestionario a personas seleccionadas, los datos secundarios y la observacin. (Prieto,2013)
                br
                br
                |La investigacin causal es ms apropiada cuando los objetivos incluyen la necesidad de entender qu variables (por ejemplo, publicidad, nmero de vendedores, precio) inciden en la variable dependiente (ventas, satisfaccin de los clientes) (Hair,2010)

      .col-lg-10
        .tarjeta-avatar-b.mb-5
          img(src='@/assets/curso/img38.svg' alt='AvatarTop').img-top
          .tarjeta.tarjeta--gris
            .p-4.py-auto.d-block
              h6 Sistemtica
              p Utilizada para evaluar un proceso mientras se va dando, encuentra el problema y propone soluciones.
    .row.mb-5.justify-content-center
      .col-lg-10
        figure.mb-5
          img.mb-3(src='@/assets/curso/img39.svg', alt='Texto que describa la imagen') 
          figcaption Fuente: Autor

    p.mb-5 En la siguiente tabla (Santos, 2017) nos da ejemplos para los diseos de investigacin.
