    h5 Fuentes de informacin
    p.mb-5 Con el fin de satisfacer las necesidades de informacin, el investigador puede recopilar datos secundarios, datos primarios o ambos. Los datos secundarios son informacin que ya existe en algn lugar, que se ha recopilado con anterioridad para otro fin. Hay muchas fuentes de datos secundarios, incluidas las fuentes internas y externas. Los datos primarios consisten en informacin recopilada para el propsito especfico en cuestin. (Rosendo, 2018)
    .row.mb-5.justify-content-center
      .col-lg-8
        figure.mb-5
          img.mb-3(src='@/assets/curso/img40.svg', alt='Texto que describa la imagen') 
    .row.mb-5.justify-content-center
      .col-lg-8
        figure.mb-5
          img.mb-3(src='@/assets/curso/img41.svg', alt='Texto que describa la imagen') 
          figcaption Fuente: Autor    
    .row.mb-5.justify-content-center
      .col-lg-4
        .cuadros--salmon.p-4
          h5.mb-0.text-white.text-center Informacin secundaria interna
        .d-flex
          .cuadros--gris-fit.d-block.p-5
            p.my-auto.d-block Todas las organizaciones tienen datos internos disponibles en los diferentes departamentos. Por ejemplo, informes de ventas, actividades de publicidad y promocin, informacin sobre clientes, anteriores proyectos de investigacin y desarrollo, informes de fabricacin, etc. Las fuentes internas tradicionales de datos secundarios son documentos tales como facturas de ventas, informes de cuentas por cobrar, ventas trimestrales, cartas/comentarios de clientes, pedidos por correo, formularios, informes de solicitudes de crdito, informes de actividades de ventas, estudios previos o formularios de gastos del personal de ventas (Hair, 2010). 
      .col-lg-4
        .cuadros--rojo.p-4
          h5.mb-0.text-white.text-center Informacin secundaria externa
        .d-flex
          .cuadros--gris-fit.d-block.p-5
            p.my-auto Los datos secundarios externos son recopilados por organismos externos, como gobiernos, asociaciones comerciales, organizaciones sin nimo de lucro, servicios de investigacin de mercados, compaas consultoras, grupos de compradores, inventarios de tiendas y publicaciones acadmicas y de inters general. Los datos secundarios tambin se encuentran en fuentes computarizadas como proveedores de informacin, sitios comerciales de internet, listas de correo o servicios de compensacin y logstica de mercadotecnia. (Hair,2010)
    p.mb-5 En Colombia se tienen muchas fuentes de datos secundarios:
