    p.mb-5 Los investigadores pueden visitar sitios en donde se encuentra variedad de datos disponibles para obtener informacin secundaria, incluida inteligencia competitiva. Aunque mucha informacin es gratis, otra, como la ofrecida por Nielsen Site Reports, debe comprarse.
      br
      br
      |Segn McDaniel (2016) las ventajas en la investigacin de mercados al usar informacin secundaria es la siguiente:
    .tarjeta--gris.d-flex.p-5
      .row
        .col-6
          .d-flex
            img(src='@/assets/curso/img42.svg' alt='AvatarTop' style='width:75px').d-inline-block
            p.d-inline-block.ms-5  Los datos secundarios pueden ayudar a aclarar o redefinir el problema durante el proceso de la investigacin exploratoria.
        .col-6
          .d-flex
            img(src='@/assets/curso/img43.svg' alt='AvatarTop' style='width:112px').d-inline-block
            p.d-inline-block.ms-5  Los datos secundarios pueden ofrecer en realidad una solucin al problema. Es muy improbable que el problema sea excepcional, existe la posibilidad de que otra persona se haya ocupado ya de un problema muy parecido. 
    div.d-flex.p-5
      .row
        .col-6
          .d-flex
            img(src='@/assets/curso/img44.svg' alt='AvatarTop' style='width:112px').d-inline-block
            p.d-inline-block.ms-4  Los datos secundarios pueden ofrecer opciones a mtodos de investigacin con datos primarios. Cada proyecto de investigacin con datos primarios est diseado para la situacin implicada; en consecuencia, el investigador de mercados siempre debe estar abierto a fuentes que sugieran opciones de investigacin. 
        .col-6
          .d-flex
            img(src='@/assets/curso/img45.svg' alt='AvatarTop' style='width:122px').d-inline-block
            p.d-inline-block.ms-5  Los datos secundarios pueden alertar al investigador de mercados de problemas o dificultades potenciales. Como tcnicas de recoleccin impopulares, dificultades con la seleccin de muestras u hostilidad de los encuestados.
    .tarjeta--gris.d-flex.p-5.mb-5
      .row
        .col-6
          .d-flex
            img(src='@/assets/curso/img46.svg' alt='AvatarTop' style='width:112px').d-inline-block
            p.d-inline-block.ms-3  Los datos secundarios pueden proporcionar informacin contextual necesaria y aumentar la credibilidad del informe de investigacin.
        .col-6
          .d-flex
            img(src='@/assets/curso/img47.svg' alt='AvatarTop' style='width:120px').d-inline-block
            p.d-inline-block.ms-5  Los datos secundarios pueden aportar el marco muestral.
    .row.mb-5.justify-content-center
      .col-lg-10
        .cuadros--salmon.p-4
          h5.mb-0.text-white.text-center Informacin secundaria en la era digital
        .d-flex
          .cuadros--gris-fit.d-block.p-5
            p.my-auto.d-block Hoy en da y al igual que con otras muchas actividades en los negocios, la existencia de Internet ha revolucionado la forma en que los datos secundarios pueden ser y son recogidos. La aparicin de una variedad de motores de bsqueda y la preeminencia de marcas como Google ha hecho la tarea de recoleccin de datos secundarios mucho ms fcil. De hecho, se podra sugerir que Internet ofrece tanta informacin no clasificada y difuminada que la bsqueda de datos secundarios en la era moderna consiste en determinar qu datos son tiles y cules deben ser desechados. 
              br
              br
              |Para los investigadores existen portales y sitios web que permiten acceder a las estadsticas gubernamentales, informes sectoriales y estadsticas tiles sobre la poblacin, comportamientos de uso, etc. Se encuentran en la identificacin de los patrones de consumo, en el seguimiento de tendencias, estimacin del potencial de mercado, previsin de ventas, seleccin de zonas comerciales, implementacin de la base de datos de clientes o el desarrollo de listas de clientes potenciales. (Dos Santos, 2017).
    h5 Anlisis de la situacin
    .row.mb-5.justify-content-center
      .col-lg-8
        figure
          img(src='@/assets/curso/img48.jpg', alt='Texto que describa la imagen') 
    p.mb-5 La informacin primaria est ligada a la generacin de nueva informacin directa desde la poblacin objetivo y consiste en buscar la informacin del fenmeno directamente en individuos que comparten una caracterstica particular respecto al fenmeno estudiado. (Neme,2017)
      br
      br
      |Los datos primarios estn directamente vinculados con la informacin que el investigador desarrolla o recopila especficamente para el proyecto de investigacin en cuestin y pueden ser investigacin cualitativa y cuantitativa. (Rosendo,2018)
    .row.mb-5.justify-content-center
      .col-lg-8
        figure
          img.mb-3(src='@/assets/curso/img49.svg', alt='Texto que describa la imagen') 
          figcaption Fuente: Autor
    p.mb-5 El recurso de internet como herramienta preferida de recoleccin de datos cuantitativos y cualitativos ha tenido el mayor impacto en el cambio de las metodologas de investigacin de mercados y de lo que esperan los clientes. las metodologas no ponen en riesgo los resultados, y dan la posibilidad de reducir el trabajo de campo. Para muchos clientes y usuarios de la investigacin de mercados, la necesidad de rapidez ha dejado de lado la necesidad de hacer anlisis exhaustivos e interpretaciones de los datos y, en algunos casos, la precisin analtica. (Hair,2010)
      br
      br
