    h3 Recoleccin de datos
    .row.mb-5.justify-content-center
      .col-lg-8
        figure
          img.mb-3(src='@/assets/curso/img50.svg', alt='Texto que describa la imagen') 
    p.mb-5 El investigador selecciona las tcnicas particulares que deben utilizarse en la solucin del problema formulado. Hay un amplio nmero de tcnicas disponibles para la recogida de datos. Algunas tcnicas son nicas para un enfoque de investigacin. La investigacin cualitativa podra ser utilizada para preceder a una investigacin cuantitativa. 
      br
      br
      |Para la recoleccin de datos se requiere el uso de personal. Las diversas tcnicas de investigacin implican variados mtodos de recopilacin de datos. Por ejemplo, las encuestas requieren la participacin directa de los encuestados. Esto implica rellenar un cuestionario o interactuar con un entrevistador. (Dos Santos,2017)
    h5 Errores de investigacin
    p.mb-5 Es importante tener en cuenta los posibles errores en los que se puede incurrir al momento de recolectar los datos para no caer en ellos, a continuacin, en la siguiente tabla se presentan los principales.
    .row.mb-5.justify-content-center
      .col-10
        .tabla-a.color-acento-botones.mb-5 
          table
            caption Fuente: (Mahotra, 2008).
            thead
              tr
                <th colspan="2" class="cuadros--azul ; text-white ;"> Diseo Investigacin </th>
                
            tbody.mb-3
              tr
                <td rowspan="6" class="cuadros--coral ; text-white ; p-5"> Errores del investigador </td>
              tr.tarjeta--gris
                td Error por sustitucin de informacin: se obtiene informacin diferente a la buscada.
              tr
                td Error de medida: ej. Se busca medir la satisfaccin y se mide la lealtad.
              tr.tarjeta--gris
                td Error de definicin de la poblacin objetivo: variacin entre la poblacin real y la poblacin definida en la investigacin
              tr
                td Error por marco muestral incompleto: ej. Bases de datos desactualizadas.
              tr.tarjeta--gris
                td Error de anlisis de datos: ej. Uso inadecuado de procedimientos estadsticos. 
            tbody.mb-3
              tr
                <td rowspan="4" class="cuadros--coral ; text-white ; p-5"> Errores del entrevistador  </td>
              tr
                td Error de seleccin de la muestra: buscan encuestados diferentes a los seleccionados en la muestra. 
              tr.tarjeta--gris
                td Error de pregunta: ej. No se utilizan palabras exactas del diccionario.  
              tr
                td Error por engao: Crea o inventa respuestas, en una o la totalidad del cuestionario. 
            tbody.mb-3
              tr
                <td rowspan="3" class="cuadros--coral ; text-white ; p-5"> Errores del encuestado </td>
              tr.tarjeta--gris
                td Error de incapacidad: Los encuestados no dan respuestas precisas, por olvido o por aburrimiento.
              tr
                td Error de resistencia: Responden mal intencionalmente por vergenza o por ser socialmente aceptable. 
    h4 Trabajo de campo        
    .row.mb-5.justify-content-center
      .col-lg-10
        figure
          img.mb-3(src='@/assets/curso/img51.jpg', alt='Texto que describa la imagen') 
    p.mb-5 El trabajo de campo abarca la seleccin, la capacitacin, la supervisin, la remuneracin y la evaluacin de las personas que recogen los datos en el terreno propio de la investigacin. La capacitacin debe ser considerada como una inversin dentro del presupuesto de la investigacin porque all radica en gran parte el xito del proyecto. Esta debe darse en tcnicas de recoleccin de datos, conocimiento profundo del cuestionario, manejo de entrevistas, solucin de preguntas y desarrollo de la empata y autoestima, entre otros temas (Prieto, 2013).
      br
      br
      |Implica operar tanto en el campo, como es el caso de entrevistas personales (en casa, en un centro comercial, o entrevistas personales asistidas por ordenador), desde una oficina por telfono o por e-mail. La seleccin apropiada, formacin y evaluacin de los trabajadores de campo ayuda a minimizar los errores en el proceso de la recogida de los datos (Rosendo, 2018).
    h4 Tcnicas de recoleccin de informacin
    .row.mb-5.justify-content-center
      .col-lg-8
        figure
          img.mb-3(src='@/assets/curso/img52.svg', alt='Texto que describa la imagen') 
          figcaption Fuente: Autor
    p.mb-5 El trabajo de campo abarca la seleccin, la capacitacin, la supervisin, la remuneracin y la evaluacin de las personas que recogen los datos en el terreno propio de la investigacin. La capacitacin debe ser considerada como una inversin dentro del presupuesto de la investigacin porque all radica en gran parte el xito del proyecto. Esta debe darse en tcnicas de recoleccin de datos, conocimiento profundo del cuestionario, manejo de entrevistas, solucin de preguntas y desarrollo de la empata y autoestima, entre otros temas (Prieto, 2013).
      br
      br
      |Implica operar tanto en el campo, como es el caso de entrevistas personales (en casa, en un centro comercial, o entrevistas personales asistidas por ordenador), desde una oficina por telfono o por e-mail. La seleccin apropiada, formacin y evaluacin de los trabajadores de campo ayuda a minimizar los errores en el proceso de la recogida de los datos (Rosendo, 2018).
    h5 Observacin
    .row.mb-5.justify-content-center
      .col-lg-6
        figure
          img.mb-3(src='@/assets/curso/img53.svg', alt='Texto que describa la imagen') 
    p.mb-5 Es una tcnica que consiste en observar a las personas cuando efectan un trabajo. La observacin tiene amplia aceptacin cientfica. Los socilogos, siclogos e ingenieros industriales utilizan extensamente sta tcnica con el fin de estudiar a las personas en sus actividades de grupo y como miembros de la organizacin. El propsito de la organizacin es mltiple: permite al analista determinar que se est haciendo, como se est haciendo, quien lo hace, cuando se lleva a cabo, cuanto tiempo toma, dnde se hace y porque se hace.
      br
      br
      |Para McDaniel (2016) La investigacin por observacin puede definirse como el proceso sistemtico de registrar patrones de ocurrencias o conductas sin normalmente cuestionar o comunicarse con las personas implicadas. En el siguiente cuadro da ejemplos de situaciones presentadas al utilizar la tcnica de observacin. 
    .row.mb-5.justify-content-center
      .col-10
        .tabla-a.color-acento-botones.mb-5 
          table
            caption Fuente: (McDaniel, 2016).
            thead
              tr
                th.cuadros--azul.text-white Situaciones
                th.cuadros--azul.text-white Ejemplos
            tbody
              tr.tarjeta--gris
                td.cuadros--oro.text-white.text-center Personas mirando a personas
                td Observadores estacionados en supermercados ven a consumidores seleccionar productos, con el propsito de saber cunto copra en el punto de venta.
              tr
                td.cuadros--oro.text-white.text-center Personas mirando fenmenos
                td Observadores estacionados en una interseccin cuentan vehculos movindose en distintas direcciones para establecer la necesidad de un semforo.
              tr.tarjeta--gris
                td.cuadros--oro.text-white.text-center Mquinas mirando a personas
                td Cmaras de video graban a consumidores seleccionando productos.
              tr
                td.cuadros--oro.text-white.text-center Mquinas mirando fenmenos
                td Programas avanzados de software registran a personas navegando en internet.
    .row.mb-5.justify-content-center
      .col-10
        ul.lista-ul--color.mb-5
            li 
              i.fas.fa-angle-right
              p
                b Tcnica.
                |  La observacin puede ser formal o informal, se puede realizar en un mercado real o se realice en un entorno controlado; directa o indirecta segn el medio empleado para la captura de informacin; e incluso consciente o encubierta en funcin del conocimiento que tenga el observado sobre la situacin. En cuanto a los procedimientos de observacin, normalmente los segmentamos en humanos o mecnicos (Sarabia Snchez, 1999). En este primer grupo los observadores son investigadores cualificados y entrenados que observan y registran la informacin. Y en el segundo grupo encontramos medios como la fotografa, cmaras de vdeo o contadores y clulas fotoelctricas o de infrarrojos. Entre los mtodos ms innovadores podemos destacar los estudios con seguimiento ocular, respuesta galvnica de la piel o anlisis facial. Los procedimientos ms usuales son los paneles de minoristas, anlisis de contenido y anlisis histrico. (Dos Santos, 2017). 
    h5 Grupos focos
    p.mb-5 Un grupo foco o grupo de discusin es una conversacin entre varias personas sentados juntos, usualmente en una mesa redonda. Una persona (sea el investigador u otro encargado) juega el papel de moderador, l inicia el debate e interviene peridicamente para mantener la sesin en el tema deseado. El moderador puede cerrar la discusin cuando considere que el tiempo ha terminado y todos los temas hayan sido abordados (Bradley, 2010)
    .row.mb-2.justify-content-center
      .col-10
        ul.lista-ul--color.mb-5
            li 
              i.fas.fa-angle-right
              p
                b Tcnica.
                |  Es una entrevista no estructurada y libre a un grupo reducido de personas, por lo general entre seis y diez. La duracin puede estar comprendida entre una hora y media, hasta las seis horas. La dinmica es moderada por un investigador capacitado, normalmente psiclogos de profesin. Este trata de fomentar el dilogo con puntos guiados interviniendo lo menos posible, y siempre evitando preguntas directas. Siempre es habitual grabar la dinmica. Entre las aplicaciones se emplea para enfocar polticas de recursos humanos, interpretaciones de marcas, uso de productos, conceptos de nuevos productos, etc. (Dos Santos, 2017). 
    .row.mb-2.justify-content-center
      .col-lg-8
        figure
          img.mb-3(src='@/assets/curso/img54.svg', alt='Texto que describa la imagen') 
          figcaption Fuente: (McDaniel, 2016).
    h5 Etnografa
    p.mb-5 Es una tcnica descriptiva, por tanto, detalla el comportamiento de las personas en ambientes naturales. El proceso etnogrfico se logra a travs de la observacin participante, en la cual el investigador se hace parte de la comunidad que est estudiando (Bautista, 2011)
      br
      br
      |Para McDaniel (2016) las ventajas de realizar investigacin etnogrfica son:
    .row.mb-2.justify-content-center
      .col-5
        .tarjeta-avatar
          img(src='@/assets/curso/img2.svg' alt='AvatarTop')
          .tarjeta.color-gris
            .p-5
              p Se basan en la realidad. Mostrando exactamente cmo viven los consumidores con un producto, no solo lo que dicen sobre l o cmo recuerdan usarlo.  
      .col-5
        .tarjeta-avatar
          img(src='@/assets/curso/img3.svg' alt='AvatarTop')
          .tarjeta.color-gris
            .p-5
              p Puede revelar necesidades y deseos no expresados.
    .row.mb-2.justify-content-center
      .col-5
        .tarjeta-avatar
          img(src='@/assets/curso/img4.svg' alt='AvatarTop')
          .tarjeta.color-gris
            .p-5
              p Puede descubrir beneficios de consumo no explotados. 
      .col-5
        .tarjeta-avatar
          img(src='@/assets/curso/img5.svg' alt='AvatarTop')
          .tarjeta.color-gris
            .p-5
              p Puede mostrar cmo, cundo, por qu y dnde la gente compra marcas, y cmo las percibe en comparacin con productos competidores.
    .row.mb-2.justify-content-center
      .col-5
        .tarjeta-avatar
          img(src='@/assets/curso/img6.svg' alt='AvatarTop')
          .tarjeta.color-gris
            .p-5
              p Puede mostrar quin en la familia realmente usa un producto y quiz descubrir un potencial objetivo demogrfico totalmente nuevo.  
      .col-5
        .tarjeta-avatar
          img(src='@/assets/curso/img7.svg' alt='AvatarTop')
          .tarjeta.color-gris
            .p-5
              p Aprovecha la experiencia de los consumidores con la categora y su creatividad prctica mientras hacen una demostracin de sus ideas de nuevos productos y mejoras de productos. 
    .row.mb-5.justify-content-center
      .col-5
        .tarjeta-avatar
          img(src='@/assets/curso/img8.svg' alt='AvatarTop')
          .tarjeta.color-gris
            .p-5
              p  Puede ayudar a formar una mejor relacin con los clientes, basada en un conocimiento ntimo de su estilo de vida. 
    .row.mb-5.justify-content-center
      .col-10
        ul.lista-ul--color.mb-5
            li 
              i.fas.fa-angle-right
              p
                b Tcnica.
                |  El investigador se sumerge en un hogar, filial de grupos de empleados, o entre los vendedores de un gran almacn. Es particularmente til cuando el segmento objetivo no puede verbalizar apropiadamente su necesidad, por ejemplo, el pblico infantil. (Dos Santos,2017) 
    h5 Entrevista
    .row.mb-2.justify-content-center
      .col-lg-8
        figure
          img.mb-3(src='@/assets/curso/img55.svg', alt='Texto que describa la imagen') 
    p.mb-5 Se utilizan para recabar informacin en forma verbal, a travs de preguntas que propone el analista. El analista puede entrevistar al personal en forma individual o en grupos algunos analistas prefieren este mtodo a las otras tcnicas que se estudiarn ms adelante. Es un intercambio de informacin que se efecta cara a cara; sirve para obtener informacin acerca de las necesidades y la manera de satisfacerlas, as como consejo y comprensin por parte del usuario para toda idea o mtodo nuevos.
    .row.mb-5.justify-content-center
      .col-10
        ul.lista-ul--color.mb-5
            li 
              i.fas.fa-angle-right
              p
                b Tcnica.
                |  Se realiza cara a cara entre dos personas, el investigador y el entrevistado. Se asemejan a las entrevistas psicolgicas y clnicas, pero con otro propsito. Son habituales en el sector industrial, donde la dispersin y nmero de clientes no aconsejan otro mtodo. Mediante la entrevista se recoge mejor la motivacin del consumidor, es ms profunda, novedosa, y subconsciente. la entrevista en profundidad de suele desarrollar en tres etapas. En la primera se realiza una preparacin adecuada, no solo de un entorno cuidadosamente elegido y confortable, tambin por la estructura o guion. En esta fase reclutamos a los individuos cuidando que no conozcan el propsito de la entrevista con antelacin para que no puedan prepararlo. La segunda fase, la entrevista, comienza con un saludo inicial y se explica el propsito de la entrevista. Se detalla el procedimiento y el proceso. La idea es crear un ambiente relajado y cmodo para expresar opiniones. Se valora la espontaneidad y la creatividad. Es conveniente grabar la conversacin para no perder tiempo en anotar todo. La tercera parte de la entrevista se reserva al anlisis y la interpretacin. (Dos Santos, 2017).
    p.mb-5 Para la investigacin cuantitativa se utiliza la encuesta como tcnica de recoleccin de informacin.    
    .row.mb-5.justify-content-center
      .col-lg-8
