      br
      br
      |De acuerdo con (Rosendo, 2018), el cuestionario es un conjunto de preguntas escritas o verbales que debe responder el encuestado. Es muy importante disear un buen cuestionario para asegurar que no se introducen errores en el conjunto de datos. Cualquier cuestionario debe tener tres objetivos principales:
