    h4 Anlisis e interpretacin de datos
    p.mb-5 El anlisis depende del tipo de informacin requerida, del diseo de la investigacin y de la calidad de los datos obtenidos. Cuando se van a examinar de forma crtica los datos se deben ordenar en categoras significativas (revisin, codificacin y tabulacin). La revisin y codificacin son funciones bsicas, porque la primera se hace necesaria para eliminar errores en los datos iniciales, lo cual se puede hacer en el terreno de campo o en la gerencia del proyecto, y la segunda agrupa los datos en las categoras correspondientes, facilitando la tabulacin. La tabulacin consiste en contar el nmero de aspectos, caractersticas, datos, etctera, que se insertan dentro de las categoras definidas por el investigador. Esta puede hacerse manual, mecnica o electrnica, dependiendo de la magnitud de los datos. (Prieto,2013)
