    p.mb-5 Segn McDaniel (2016) lo primero que se debe hacer es realizar un proceso de validacin y edicin de la informacin recolectada, en donde se confirma que las entrevistas se realizaron de acuerdo con lo especicado para detectar fraudes o fallas del entrevistador y se revisa si existen preguntas con respuestas sin registrar.
      br
      br
      |Segundo se codifican las preguntas, se refiere al proceso de agrupar y asignar cdigos numricos a las respuestas a una pregunta particular, tercero se ingresan los datos dentro de un sistema en donde se realiza el proceso de convertir la informacin a formatos electrnicos, el cuarto paso es depurar la informacin repetida o mal codificada y el ltimo paso es tabulacin y anlisis estadstico.
    h4 Comunicacin de los resultados de la investigacin
    p.mb-5 Un reporte eficaz de investigacin de mercados es una forma de asegurar que el tiempo, esfuerzo y dinero que se destinaron al proyecto de investigacin se aprovecharn completamente. (Hair,2010).
      br
      br
      |Para comunicar los resultados de la investigacin primero se debe realizar un resumen ejecutivo que es la porcin del informe que explica qu descubri la investigacin, qu significan los resultados y qu accin, debe emprenderse, con base en los hallazgos de la investigacin; segundo se formulan las conclusiones que son generalizaciones que responden las preguntas planteadas en los objetivos de la investigacin; Tercero se dan las recomendaciones ya sean generales o especficas y por ltimo se presenta el informe. (McDaniels,2016)
    .row.mb-5.justify-content-center
      .col-lg-8
        figure
          img.mb-3(src='@/assets/curso/img69.svg', alt='Texto que describa la imagen') 
          figcaption Fuente: Autor
    p.mb-5 Un informe es completo cuando ofrece al lector toda la informacin que necesita en un lenguaje claro y conciso, no necesita estar sobrecargado con detalles sin importancia, debe enfocarse en los puntos principales de la investigacin e incluir la informacin adicional, si es necesario, en los apndices. Los directivos, normalmente, no tienen tiempo para leer pginas y pginas de informacin, necesitan informes que contengan la informacin requerida sin ser demasiado extensos. Un informe es preciso cuando la informacin que contiene es exacta. Es muy importante verificar la exactitud del informe antes de que se distribuya, de lo contrario podra conducir a decisiones directivas errneas. Siempre es una buena idea escribir una versin preliminar y verificar todos los detalles a fondo. Un informe es claro cuando sigue una secuencia lgica de pensamiento, la redaccin es precisa y fluida y est bien estructurado. (Rosendo, 2018).
    h5 Seguimiento y control
    p.mb-5 Para Malhotra (2008), el investigador debe ayudar al cliente a entender ya que es posible que surjan muchas preguntas, luego puede contribuir a poner en prctica los hallazgos en la seleccin de una agencia de publicidad, un nuevo producto o incluso una poltica de precios, es importante que el proyecto de investigacin de mercados forme parte de un sistema de informacin de <i>marketing</i> SIM. 
